.about-project {
  margin: 0 auto;
  width: 100%;
  background: black;
}

.about-project__container {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-width: 1280px;
  
}

.about-project__title {
  margin: 0 auto;
  width: calc(100% - 70*2px);
  margin-top: 110px;
  color: #cfa971;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.88px;
  font-family: 'Inter', 'Arial', sans-serif;
  text-align: center;
}

.about-project__line {
  margin: 0 auto;
  width: calc(100% - 70*2px);
  margin-top: 23px;
  height: 1px;
  background-color: #cfa971;
}

.about-project__stages-project {
  margin: 0 auto;
  width: calc(100% - 70*2px);
  margin-top: 70px;
  display: flex;
  flex-direction: row;
  column-gap: 45px;
  margin-bottom: 40px;
  padding: 10px;
}

.about-project__about-include {}

.about-project__about-include-title {
  margin: 0;
  color: #cfa971;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.8px;
  text-align: center;
}

.about-project__about-include-subtitle {
  margin: 0;
  margin-top: 26px;
  color: #cfa971;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
}

.about-me__github {
  margin: 0 auto;
  text-decoration: none;
  margin-top: 99px;
  color: #cfa971;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  background-color: #272727;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  margin-bottom: 20px;
}

@media screen and (max-width: 830px) {
  .about-project__about-include-subtitle {
    margin-top: 22px;
    font-size: 12px;
    line-height: 18px;
  }

  .about-project__title {
    margin-top: 90px;
    width: calc(100% - 50*2px);

  }

  .about-project__subtitle {
    margin-bottom: 92px;
    width: calc(100% - 50*2px);
  }

  .about-project__time-project {
    margin-top: 90px;
    width: calc(100% - 50*2px);
  }


  .about-project__line {
    width: calc(100% - 50*2px);
  }

  .about-project__stages-project {
    column-gap: 30px;
    width: calc(100% - 50*2px);
  }

  .about-me__github {
    margin-top: 87px;
  }
}

@media screen and (max-width: 530px) {
  .about-project__title {
    margin-top: 40px;
    font-size: 18px;
    letter-spacing: -0.72px;
    width: calc(100% - 18*2px);
  }

  .about-project__subtitle {
    margin-bottom: 107px;
    width: calc(100% - 18*2px);
  }

  .about-project__line {
    margin-top: 28px;
    width: calc(100% - 18*2px);
  }

  .about-project__stages-project {
    flex-direction: column;
    margin-top: 30px;
    row-gap: 56px;
    width: calc(100% - 18*2px);
    margin-bottom: 20px;
  }

  .about-project__about-include-title{
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.72px;
  }

  .about-project__about-include-subtitle {
    margin-top: 16px;
    font-size: 12px;
    line-height: 16px;
  }

  .about-project__backend-subtitle {
    font-size: 11px;
    width: 148px;
  }

  .about-project__time-project {
    margin-top: 60px;
    width: calc(100% - 18*2px);
  }

  .about-project__subtitle {
    margin-bottom: 62px;
  }

  .about-me__github {
    margin-top: 10px;
  }

}