.program {
  background: #272727;
}

.program__container {
  margin: 0 auto;
  margin-top: 40px;
  max-width: 1280px;
  display: flex;
  flex-direction: column;
  width: calc(100% - 70*2px);
}

.program__title {
  margin: 0;
  margin-left: 70px;
  margin-top: 60px;
  max-width: 630px;
  color: #cfa971;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.88px;
  text-align: center;
}

.program__subtitle{
  margin: 0 auto;
  margin-top: 60px;
  color: #FFF;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.8px;
}

.program__line {
  margin: 0 auto;
  margin-top: 23px;
  width: calc(100% - 70*2px);
  height: 1px;
  background-color: #DADADA;
}

.program__items{
  background-color: #272727;
  border-radius: 10px;
  margin-top: 10px;
}

.program__items-title{
  margin: 0;
  padding: 10px;
  margin-left: 70px;
  margin-top: 60px;
  max-width: 630px;
  color: #cfa971;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.88px;
  background-color: black;
  border-radius: 10px;
}

.program__items-subtitle{
  margin: 0 auto;
  margin-top: 26px;
  margin-bottom: 10px;
  color: black;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  background-color: #B0B0B0;;
  border-radius: 10px;
  padding: 10px;
}

.program__items:last-child{
  margin-bottom: 30px;
}

@media screen and (max-width: 830px) {
  .program__container {
    width: calc(100% - 50*2px);
  }
  .program__title {
    margin-top: 90px;
    margin-left: 50px;
  }

  .program__items-title{
    margin-top: 90px;
    margin-left: 50px;
  }


  .program__line {
    width: calc(100% - 50*2px);
  }
}

@media screen and (max-width: 530px) {

  .program__container {
    width: calc(100% - 18*2px);
  }

  .program__title {
    margin-top: 40px;
    margin-left: 0px;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.72px;
  }

  .program__items-title{
    margin-top: 10px;
    margin-left: 0px;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.72px;
    text-align: center;
  }

  .program__line {
    width: 100%;
    background-color: #DADADA;
    margin-top: 28px;
  }
}