.results {
  margin: 0 auto;
  margin-top: 40px;
  max-width: 1280px;
}

.results__container {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: calc(100% - 70*2px);
  margin-bottom: 30px;
}

.results__title {
  margin: 0 auto;
  color: #cfa971;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.8px;
}

.results__subtitle {
  margin: 0 auto;
  margin-top: 26px;
  color: #cfa971;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}

.results__videos {
  margin: 0 auto;
  margin-top: 20px;
  height: 150px;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #dadada;
  list-style-type: none;
  padding: 0;
}

.results__video{
  list-style-type: none;
}

@media screen and (max-width: 830px) {
  .results__container {
    width: calc(100% - 50*2px);
  }
}

@media screen and (max-width: 530px) {
  .results__container {
    width: calc(100% - 18*2px);
  }
}