.promo {
  margin: 0 auto;
  /* width: calc(100% - 40*2px); */
  /* margin-top: 20px; */
  border-radius: 10px;
  background-image: url('../../images/pavel-main2.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.promo__container {
  margin: 0 auto;
  max-width: 1280px;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* background-color: #272727; */
  border-radius: 10px;
}

.promo__title {
  margin: 0;
  margin-top: 540px;
  margin-bottom: 30px;
  color: #cfa971;
  text-align: center;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 50px;
  font-style: normal;
  font-weight: 400;
  line-height: 58px;
  letter-spacing: -2px;
  max-width: 730px;
}

.promo__subtitle {
  margin: 0;
  margin-top: 10px;
  color: #FFF;
  text-align: center;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  line-height: 58px;
  letter-spacing: -2px;
  max-width: 730px;
}

.promo__buttons {
  display: flex;
  flex-direction: row;
  margin-top: 15px;
  column-gap: 10px;
  margin-bottom: 30px;
}


.promo__button {
  margin: 0;
  padding: 10px 17px;
  border: none;
  border-radius: 10px;
  background: #cfa971;
  color: #000000;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  text-decoration: none;
}

.promo__button:hover {
  cursor: pointer;
}

@media screen and (max-width: 830px) {
  .promo {
    background-size: cover;
  }

  .promo__buttons {
    margin-top: 15px;
  }

  .promo__title {
    margin-top: 440px;
    max-width: 680px;
    font-size: 40px;
    line-height: 52px;
    letter-spacing: -1.6px;
  }
}

@media screen and (max-width: 530px) {
  .promo {
    background-size: 100% 100%;
  }

  .promo__title {
    margin-top: 430px;
    text-align: center;
    font-size: 29px;
    line-height: 37px;
    letter-spacing: -1.16px;
  }

  .promo__buttons {
    margin-top: 20px;
    margin-bottom: 17px;
    column-gap: 6px;
  }

  .promo__button {
    box-sizing: border-box;
    font-size: 14px;
    width: 142px;
    padding: 10px 15.5px;
    text-align: center;
  }

  .promo__subtitle {
    font-size: 20px;
  }

}