.questions{
  background: black;
}

.questions__container{
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: calc(100% - 70*2px);
  margin-bottom: 30px;
}

.questions__title{
  margin: 0;
  margin-left: 70px;
  margin-top: 60px;
  max-width: 630px;
  color: #cfa971;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.88px;
}

.questions__line{
  margin: 0 auto;
  margin-top: 23px;
  width: calc(100% - 70*2px);
  height: 1px;
  background-color: #cfa971;
}



@media screen and (max-width: 830px) {
  .questions__container {
    width: calc(100% - 50*2px);
  }
  .questions__title {
    margin-top: 90px;
    margin-left: 50px;
  }

  .questions__line {
    width: calc(100% - 50*2px);
  }
}

@media screen and (max-width: 530px) {
  .questions__container {
    width: calc(100% - 18*2px);
  }

  .questions__title {
    margin-top: 40px;
    margin-left: 0px;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.72px;
    text-align: center;
  }

  .questions__line {
    width: 100%;
    margin-top: 28px;
  }
}