.accordion {
  max-width: 600px;
  margin: 2rem auto;
  transition: all 0.5s;
}

.accordion-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  cursor: pointer;
  background-color: #272727;
}

.accordion__title{
  color: #cfa971;
  margin: 0;
}

.accordion__icon{
  color: #cfa971;
}

.accordion-title:hover {
  background-color: #272727;
}

.accordion-title,
.accordion-content {
  padding: 1rem;
}

.accordion-content {
  background-color: #dadada;
  color: black;
}

@media screen and (max-width: 700px) {
  .accordion {
    width: 100%;
  }
}