.form-data {}

.form-data__container {
  display: flex;
  flex-direction: column;
  max-width: 1280px;
  margin: 0 auto;
  width: calc(100% - 40*2px);
}

.form-data-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  margin-bottom: 30px;
  width: 100%;
}

.form-data-input {
  display: flex;
  margin: 0 auto;
  margin-bottom: 10px;
  padding: 0;
  display: flex;
  width: 200px;
  border: none;
  padding: 10px;
}

.form-data__title {
  margin: 0;
  text-align: center;
  margin-bottom: 30px;
  margin-top: 60px;
  color: #cfa971;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.88px;
}

.form-data__button {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  background-color: #cfa971;
  border: none;
  border-radius: 10px;
  padding: 10px;
  color: black;
  font-family: 'Inter', 'Arial', sans-serif;
  font-size: 22px;

}

@media screen and (max-width: 830px) {
  .form-data__container {
    width: calc(100% - 40*2px);
  }

  .form-data__title {
    margin-top: 90px;
    margin-left: 50px;
  }

  .form-data__button {}
}

@media screen and (max-width: 530px) {
  .form-data__container {
    width: calc(100% - 18*2px);
  }

  .form-data__title {
    margin-top: 20px;
    margin-left: 0px;
    text-align: center;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.72px;
  }

  .form-data__button {
    width: calc(100% - 30*2px);
  }

  .form-data-input {
    width: calc(100% - 30*2px);
  }
}