.header-nav {
  margin-top: 10px;
  height: 30px;
  width: calc(100% - 20px);
  position: fixed;
  z-index: 50;
  display: flex;
  align-items: center;
  color: #cfa971;
}

.burger-btn {
  display: flex;
  justify-content: center;
  width: 30px;
  height: 30px;
  margin-left: 10px;
  position: relative;
  cursor: pointer;
  background-color: #cfa971;
  border-radius: 50%;
}

.burger-btn:before {
  content: '';
  position: absolute;
  top: 10px;
  width: 20px;
  background-color: black;
  height: 2px;
}

.burger-btn span {
  position: absolute;
  top: 14px;
  width: 20px;
  background-color: black;
  height: 2px;
}

.burger-btn:after {
  content: '';
  position: absolute;
  bottom: 10px;
  width: 20px;
  background-color: black;
  height: 2px;
}